@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;padding:5em 5em;background-color:#1c2230}@media (max-width: 768px){.navbar{padding:3em 1em}}@media (max-width: 498px){.navbar{justify-content:center}}@media (max-width: 498px){.navbar .brandNav{margin:1em 0}}.navbar ul{display:flex}.navbar li{margin:0 1.8em}.navbar a{font-family:"Raleway", sans-serif;color:#fff;font-weight:400;font-size:1rem;opacity:0.7}.navbar a:hover{opacity:1}.navbar img{display:flex}

.header{font-family:"Raleway", sans-serif;background-image:url(/fylodarkmode/static/media/bg-curvy-desktop.376a8b95.svg);background-position:bottom center;background-repeat:no-repeat;background-size:100% 320px;padding-bottom:10em;background-color:#1c2230}@media (max-width: 768px){.header{background-image:url(/fylodarkmode/static/media/bg-curvy-mobile.bb63956f.svg)}}.header .inner-header{width:650px;margin:0 auto;text-align:center}@media (max-width: 768px){.header .inner-header{width:90%}}.header img{width:100%;height:auto;margin:0 0 1.5em 0}.header h1{font-size:2.6rem;margin:0.35em 0}.header p{font-family:"Open Sans", sans-serif;font-size:1.25rem;width:82%;margin:1.5em auto;color:#ccd3da;line-height:2rem}.header button{padding:0.875em 5em;background-color:#339ecc;border:1px solid transparent;color:#fff;border-radius:25px;font-weight:700;font-size:1.15rem;cursor:pointer;transition:0.4s}.header button:hover{background-color:transparent;color:#339ecc;border:1px solid #339ecc}

.features{background-color:#181f2b}.features .in-features{display:flex;flex-wrap:wrap;justify-content:space-between;width:900px;margin:0 auto}@media (max-width: 868px){.features .in-features{flex-direction:column;width:100%}}.features .card{text-align:center;width:350px;margin:2em auto}@media (max-width: 440px){.features .card{width:95%}}.features .card img{margin:1em 0}.features .card h3{font-size:1.5rem}.features .card p{font-family:"Open Sans", sans-serif;margin:0.75em 0;color:#ccd3da;font-size:0.95rem}.features .f-2{display:flex;justify-content:space-around;align-items:center;padding:4em}@media (max-width: 868px){.features .f-2{flex-direction:column}}.features .f-2>div{flex-basis:45%}.features .f-2 .stay img{width:100%;height:auto}@media (max-width: 868px){.features .f-2 .in-f-2{text-align:center;margin-top:2em}}.features .f-2 .in-f-2 h1{font-family:"Raleway", sans-serif;font-size:2.5rem}.features .f-2 .in-f-2 p{font-family:"Open Sans", sans-serif;font-size:1.15rem;margin:1em 0;color:rgba(255,255,255,0.7)}.features .f-2 .in-f-2 a{border-bottom:1px solid #339ecc;color:#339ecc;font-size:1.15em;font-weight:500;padding:0.5em 0;display:inline-block;transition:0.4s}.features .f-2 .in-f-2 a:hover{opacity:0.7}.features .f-2 .in-f-2 a img{margin:0 0 0 0.25em;vertical-align:middle}

.speach{background-color:#181f2b;font-family:"Open Sans", sans-serif;padding:8em 0 2em 0}.speach .inner-speach{position:relative;display:flex;justify-content:center;flex-wrap:wrap;width:90%;margin:0 auto}.speach .inner-speach:before{content:",,";position:absolute;top:-50px;left:-10px;font-size:15rem;z-index:1;color:#5ed4cc;font-family:"Raleway", sans-serif;transform:rotate(180deg)}.speach .card{z-index:999;width:350px;background-color:#21293c;padding:1.5em 1.75em;box-shadow:5px 5px 2px 2px rgba(255,255,255,0.01);margin:1em 1.5em;border-radius:5px}.speach .card>p{font-size:1rem;color:rgba(255,255,255,0.8);margin:1.25em 0;font-weight:500;line-height:1.5rem}.speach .card .profile{display:flex;align-items:center;height:auto;margin:1em 0 0 0}.speach .card .profile img{width:30px;height:30px;border-radius:50%;margin:0 0.5rem 0 0}.speach .card .profile h4{font-size:0.85rem}.speach .card .profile p{font-size:0.65rem}.speach .access{width:60%;margin:0 auto;text-align:center;box-shadow:5px 5px 5px 5px rgba(0,0,0,0.1);padding:2.5em 2em;border-radius:5px;transform:translateY(10em);background-color:#1c2230}@media (max-width: 768px){.speach .access{width:80%}}@media (max-width: 498px){.speach .access{width:90%}}.speach .access h1{font-family:"Raleway", sans-serif}.speach .access p{font-family:"Open Sans", sans-serif;width:80%;margin:2em auto;color:rgba(255,255,255,0.7)}.speach .access input{padding:1em 2em;width:60%;border-radius:25px}@media (max-width: 868px){.speach .access input{width:80%}}.speach .access input::-webkit-input-placeholder{opacity:0.5}.speach .access button{padding:1em 2em;background-color:#339ecc;color:#fff;border:1px solid transparent;border-radius:25px;margin:0 2em;font-weight:600;cursor:pointer;transition:0.4s}@media (max-width: 868px){.speach .access button{display:block;margin:1em auto;width:80%}}.speach .access button:hover{background-color:transparent;color:#339ecc;border:1px solid #339ecc}

.footer{background-color:#0c1524;padding:12em 8em 6em}@media (max-width: 700px){.footer{padding:12em 2em 6em 2em}}.inner-footer{display:flex;justify-content:space-between;align-items:flex-start;font-family:"Open Sans", sans-serif;flex-wrap:wrap;color:rgba(255,255,255,0.8)}@media (max-width: 440px){.inner-footer{flex-direction:column}}.inner-footer li{font-size:1.2rem}.inner-footer a{color:#fff;color:rgba(255,255,255,0.8);transition:0.4s}.inner-footer a:hover{color:#123eff}.inner-footer>div{margin:1em 0}.inner-footer .ft-1{width:340px;min-width:300px}.inner-footer .ft-1 .address{display:flex}.inner-footer .ft-1 .address img{width:20px;height:20px;margin:1em 1em 0 0}.inner-footer .ft-1 .address p{line-height:1.5rem;font-size:1rem;font-weight:500}@media (max-width: 440px){.inner-footer .ft-1{width:100%;min-width:100%}}.inner-footer .ft-2 .phone{display:flex}.inner-footer .ft-2 .email{display:flex;margin:1em 0}.inner-footer .ft-2 img{margin:0 1.5em 0 0}.inner-footer .ft-3 li{margin:0 0 0.5em 0}.inner-footer .ft-4 li{margin:0 0 0.5em 0}@media (max-width: 440px){.inner-footer .ft-5{margin:1em auto}}.inner-footer .ft-5 ul{display:flex;font-size:2rem}.inner-footer .ft-5 a{margin:0 0.25em;display:flex;justify-content:center;align-items:center;width:40px;height:40px;color:#fff;border:1px solid #fff;border-radius:50%;padding:1rem;transition:0.4s}.inner-footer .ft-5 a:hover{color:#123eff;border:1px solid #123eff}

*{margin:0;padding:0;box-sizing:border-box}body,html{font-size:14px;overflow-x:hidden;color:#fff}li{list-style:none;list-style-type:none}a{text-decoration:none}button,input{border:0}button:focus,input:focus{outline:none}body{color:#fff}

